<script>
  import { onMount } from "svelte";
  import { get } from "svelte/store";

  import {
    nickname,
    csrfToken,
    getCsrfToken,
    jwtoken,
    nicknames,
    fetchNicknames,
    mode,
  } from "../store.js";
  let player;
  let playerscore;
  function handle_player_change(event) {
    player = event.target.value;
  }
  function handle_playerscore_change(event) {
    playerscore = event.target.value;
  }

  onMount(async () => {
    await getCsrfToken();
    fetchNicknames($mode); //  
  });

  async function submit_bonus_score() {
    const token = get(csrfToken); // Svelte store   

    const checkjwt = get(jwtoken);
    if (checkjwt) {
      const data = { player: player, playerScore: playerscore };

      try {
        const response = await fetch("/submit-admin-score", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
            "CSRF-Token": token,
          },
          body: JSON.stringify(data), // Send the JSON data as the body of the request
        });
        if (response.ok) {
          alert("  ");
        } else {
          alert(" ");
        }
      } catch (error) {
        alert("  :", error);
      }
    } else {
      alert(" ");
    }
  }

  async function score_reset() {
    let userResponse = confirm(
      "   . ?"
    );

    if (userResponse) {
      const token = get(csrfToken); // Svelte store   
      const checkjwt = get(jwtoken);
      if (checkjwt) {
        try {
          const response = await fetch("/reset-rank", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${checkjwt}`,
              "CSRF-Token": token,
            },
          });
          if (response.ok) {
            alert(" ");
          } else {
            alert(" ");
          }
        } catch (error) {
          alert("  :", error);
        }
      } else {
        alert(" ");
      }
    }
  }
</script>

<div class="main_data">
  {#if $nickname === "admin"}
    <div class="left">
         
      <select
        bind:value={player}
        on:change={handle_player_change}
        class="namewidth"
      >
        {#each $nicknames as option}
          <option value={option}>{option}</option>
        {/each}
      </select>
      <input
        type="number"
        bind:value={playerscore}
        on:change={handle_playerscore_change}
        placeholder=""
        class="scorewidth"
      />
      <button on:click={() => submit_bonus_score()}> </button>
      <br />
      <button on:click={() => score_reset()}>  </button>
    </div>
  {:else}
    <div class="left"> .</div>
  {/if}
</div>
