<script>
  import {
    csrfToken,
    getCsrfToken,
    jwtoken,
    mode,
    clickformopen,
  } from "./store.js";
  import { get } from "svelte/store";
  import { onMount } from "svelte";

  let oldpw = "";
  let newpw = "";
  let checkpw = "";

  onMount(async () => {
    await getCsrfToken();
  });

  async function changepw() {
    const token = get(csrfToken); // Svelte store   

    const pwdata = {
      nowpw: oldpw,
      newpw: newpw,
    };
    const checkjwt = get(jwtoken);
    if (checkjwt) {
      try {
        if (newpw !== checkpw) {
          alert("     ");
          return;
        } else {
          const endpoint = $mode ? "/process_changepw_m" : "/process_changepw";
          const response = await fetch(endpoint, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${checkjwt}`,
              "CSRF-Token": token,
            },
            body: JSON.stringify(pwdata), // Send the JSON data as the body of the request
          });
          if (response.ok) {
            alert("  ");
            clickformopen(null);
          } else {
            alert(" ");
          }
        }
      } catch (error) {
        alert("  :", error);
      }
    } else {
      alert(" ");
    }
  }
</script>

<div class="left">
  
  <input class="namewidth" type="password" bind:value={oldpw} />
   
  <input class="namewidth" type="password" bind:value={newpw} />
   
  <input class="namewidth" type="password" bind:value={checkpw} />
  <button on:click={() => changepw()}> </button>
</div>
